// common admin (new and legacy)
@import "vars";
@import "mixins";

html[dir="rtl"] .image-choices-admin-field.image-choices-use-images {
	input[type="radio"],
	input[type="checkbox"] {
		left: unset;
		right: -9999px;
	}
}

.ic-sr {
	@include accessibility;
}

.ic-icon {
	display: inline-block;
	overflow: hidden;
	padding: 0;
	line-height: 0;
	font-size: 0;
	text-indent: -9999px;
	@include icon-base;
}
button .ic-icon {
	vertical-align: middle;
}
button span:not(.ic-sr) + .ic-icon,
button .ic-icon + span:not(.ic-sr) {
	margin-left: 6px;
}

.ic-btn {
	display: inline-block;
	vertical-align: middle;
	white-space: nowrap;
	font-family: $font-family;
	font-weight: $font-weight-bold;
	font-size: 12px;
	line-height: 1.2;
	cursor: pointer;
	border: none;
	margin: 0;
	padding: 10px 20px;
	border-radius: 20px;
	min-width: 100px;
	max-width: 100%;
	position: relative;
	outline: none;
	background-color: $color-primary;
	color: $color-light;
	text-align: center;
	transition: all 0.3s;
}
.ic-btn-secondary {
	background-color: $color-dark;
}

.ic-large-btn {
	font-size: 15px;
	line-height: 1.2;
}

.ic-plain-btn,
.ic-text-btn,
.ic-circle-btn {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding: 0;
}

.ic-text-btn,
.ic-plain-btn {
	border-radius: 0;
	margin: 0;
	border: none;
	background-color: transparent;
	.ic-icon {
		margin-top: -1px;
	}
}

.ic-text-btn {
	font-weight: bold;
	font-size: 11px;
	color: $color-primary;
	.ic-icon {
		width: 14px;
		background-color: $color-primary;
	}
}

.ic-plain-btn {
	width: 24px;
	height: 24px;
	.ic-icon {
		@include position-centered(inline-block);
		background-color: $color-blue-grey;
	}
}

.ic-circle-btn {
	width: 36px;
	height: 36px;
	border-radius: 50%;
	border: 1px solid $color-blue-grey;
	background-color: #ffffff;
	box-shadow: 1px 2px 7px 0 rgba(7, 30, 87, 0.10);
	.ic-icon {
		width: 13px;
		background-color: $color-dark;
		margin-top: 0;
	}
}

.ic-btn,
.ic-text-btn,
.ic-plain-btn {
	&:before {
		pointer-events: none;
		display: inline-block;
		position: absolute;
		width: 24px;
		height: 24px;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: transparent url(../images/spinner.svg) 50% 50% no-repeat;
		background-size: contain;
	}
	&.ic-busy,
	&.ic-waiting {
		pointer-events: none;
		background-color: transparent;
		border-color: transparent;
		.ic-btn-label,
		.ic-icon {
			opacity: 0;
		}
		&:before {
			content: "";
		}
	}
}


.ic-select-icon {
	display: inline-block;
	width: 20px;
	height: 20px;
	vertical-align: top;
	overflow: hidden;
	background: transparent url(../images/icons/icon-image-choices.svg) 50% no-repeat;
	background-size: contain;
}

.media-modal {
	z-index: 1000001 !important;// 1 higher than GF new toolbar
}

.choices_setting.field_setting,
.gquiz-setting-choices.field_setting {
	position: relative;
}

.image-choices-remove-choices-btn {
	float: right;
}

.choices_setting,
.gquiz-setting-choices {
	@include clearfix;
}

.button.image-choices-option-upload-button {
	line-height: 1.4;
	font-size: 11px;
	margin-top: 4px;
	margin-left: 4px;
	height: 26px;
}

.image-choices-option-preview-wrap {
	border: 1px solid #DFDFDF;
	border-radius: 3px;
	padding: 5px;
	vertical-align: middle;
	line-height: 0;
	position: relative;
	margin: 0 5px;
	width: 28px;
	height: 28px;
	&:hover {
		.image-choices-option-preview {
			opacity: 0.7;
		}
		.image-choices-option-image-remove {
			display: block;
		}
	}
}

.image-choices-option-preview {
	width: 50px;
	height: 50px;
	display: inline-block;
	background-repeat: no-repeat;
	background-position: 50%;
	background-size: contain;
	line-height: 0;
}

.image-choices-option-image-remove {
	content: "";
	display: none;
	overflow: hidden;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: black;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -15px 0 0 -15px;
	cursor: pointer;
	z-index: 9;
	text-align: center;
	text-decoration: none;
	i {
		color: white;
		line-height: 30px;
		font-size: 20px;
	}
}


/* GF 2.5+ */
#field_settings_container,
#gfield_settings_choices_container,
#gquiz_gfield_settings_choices_container {

	.image-choices-option-preview-wrap {
		margin: 0 8px 0 5px;
		padding: 3px;
	}
	.image-choices-option-preview {
		width: 30px;
		height: 30px;
	}
	.image-choices-option-image-remove {
		width: 20px;
		height: 20px;
		margin: -10px 0 0 -10px;
		i {
			line-height: 20px;
			font-size: 15px;
		}
	}

	.button.image-choices-option-upload-button {
		border-radius: 3px;
		font-size: 11px;
		//border: 1px solid #ffffff;
		border: 1px solid #DFDFDF;
		padding: 3px;
		margin: 0 8px 0 5px;
		line-height: 21px;
		vertical-align: middle;
		width: 36px;
		height: 36px;
	}

	&.image-choices-field-settings {

		#gfield_settings_choices_container,
		.gfield_settings_input_choices_container {
			max-height: none;
			margin-bottom: 20px;
		}

		.image-choices-enabled {

			.gfield_choice_header_label {
				padding-left: 50px;
			}
			.field-choice-input.field-choice-text {
				width: 180px !important
			}

			&.choice_with_value {
				.gfield_choice_header_value,
				.gfield_choice_header_label,
				.field-choice-input.field-choice-text,
				.field-choice-input.field-choice-value {
					width: 90px !important;
				}
				.gfield_choice_header_value {
					padding-left: 0;
				}
			}
			&.choice_with_price {
				.gfield_choice_header_label,
				.gfield_choice_header_price,
				.field-choice-input.field-choice-text,
				.field-choice-input.field-choice-price {
					width: 90px !important;
				}
				.gfield_choice_header_price {
					padding-left: 3px;
				}
			}
			&.choice_with_value_and_price {
				.gfield_choice_header_label,
				.field-choice-input.field-choice-text {
					width: 70px !important;
				}
				.gfield_choice_header_value,
				.field-choice-input.field-choice-value {
					width: 70px !important;
				}
				.gfield_choice_header_price,
				.field-choice-input.field-choice-price {
					width: 60px !important;
				}
				.gfield_choice_header_price {
					padding-left: 3px;
				}
			}

		}

	}

}

// theme preview
#image-choices-theme-preview,
#product-image-theme-preview {
	background-color: #F8F8F9;
	border-radius: 5px;
	text-align: center;
	img {
		//margin: 10px;
		border-radius: 5px;
		display: none;
		width: 100%;
		max-width: 100%;
		height: auto;
	}
}
#gform_setting_gf_image_choices_theme_preview .image-choices-theme-preview img {
	width: 400px;
}

// feature colour
.gforms_edit_form .image-choices-setting-feature-color .wp-picker-container,
#gform_setting_gf_image_choices_feature_color_custom .wp-picker-container,
#gform_setting_gf_image_choices_global_feature_color_custom .wp-picker-container {
	margin-top: 0;
	.wp-color-result.button {
		height: auto;
	}
	.wp-picker-clear.button {
		min-height: 0;
		line-height: 0.875rem;
		height: 30px;
	}
}

label[for="image-choices-feature-color-custom"] {
	margin-top: 0.9375rem;
}


// new editor
textarea[id^="gf_image_choices"] ~ .CodeMirror {
	background: #f7f9fc;
	border-radius: 10px;
	padding: 20px;
	.CodeMirror-sizer {
		min-height: 300px;
	}
	.CodeMirror-gutters {
		background-color: #f7f9fc;
		border-right-color: #f7f9fc;
	}
	.CodeMirror-linenumber {
		padding: 0 10px 0 5px;
		min-width: 10px;
		text-align: left;
		color: #999;
	}
}

