@mixin accessibility{
	border:0!important;
	clip:rect(0 0 0 0)!important;
	height:1px!important;
	margin:-1px!important;
	overflow:hidden!important;
	padding:0!important;
	position: absolute!important;
	width:1px!important;
}

@mixin clearfix() {
	&:after{
		content:"";
		display:table;
		clear:both;
	}
}

@mixin input-decoration() {
	border-radius: 5px;
	border: 1px solid $color-blue-grey;
	background: #fff;
	box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.07);
}

@mixin icon-base() {
	width: 20px;
	height: auto;
	aspect-ratio: 1 / 1;
	mask-size: contain;
	mask-position: center;
	mask-repeat: no-repeat;
	background-color: $color-mid;
	transition: background-color 0.3s;
}

@mixin position-centered($display:null) {
	@if ($display) {
		display: $display;
	}
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}


@mixin icon($icon, $color:null, $size:null) {
	@if ($icon) {
		mask-image: url($icon);
		-webkit-mask-image: url($icon);
	}
	@if ($color) {
		background-color: $color;
	}
	@if ($size) {
		width: $size;
	}
}

@mixin media-query($media-query){

	@if $media-query == small {
		@media only screen and (max-width: 479px) { @content; }
	}

	@if $media-query == medium {
		@media only screen and (min-width: 480px) and (max-width: 1079px) { @content; }
	}

	@if $media-query == medium-and-up {
		@media only screen and (min-width: 480px) { @content; }
	}

	@if $media-query == small-and-medium {
		@media only screen and (max-width: 1079px) { @content; }
	}

	@if $media-query == large {
		@media only screen and (min-width: 1080px) { @content; }
	}

	@if $media-query == extra-large {
		@media only screen and (min-width: 1440px) { @content; }
	}

	@if $media-query == super-large {
		@media only screen and (min-width: 1920px) { @content; }
	}

}

