@mixin accessibility{
	visibility: hidden;
	position: absolute;
	left: -9999px;
}

@mixin clearfix() {
	&:after{
		content:"";
		display:table;
		clear:both;
	}
}

@mixin legacy_image_choices_field() {

	// GF < 2.5
	ul.gfield_radio,
	ul.gfield_checkbox {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	// GF 2.5+
	div.gfield_radio,
	div.gfield_checkbox {
		margin: 0;
		padding: 0;
	}

	.image-choices-choice {
		display: inline-block;
		margin: 0 10px 10px 0 !important;
		transition: all 0.3s;
		border: 1px solid rgba(#DFDFDF, 0);
		vertical-align: top;
		text-align: center;
		position: relative;
		label {
			cursor: pointer;
			display: inline-block !important;// added !important for GF 2.5+
			line-height: 0;
			margin: 0 !important;
			max-width: none !important;
			padding: 10px !important;// needed for entry detail view
			text-align: center;
			width: 100% !important;
			border: 1px solid transparent;
		}
		input[type="radio"],
		input[type="checkbox"] {
			@include accessibility;
		}
	}

	.image-choices-choice-text,
	.image-choices-choice-price {
		font-size: 12px;
		font-weight: normal;
		line-height: 1.4;
		padding-top: 6px;
		transition: all 0.3s;
	}
	.image-choices-choice-price {
		display: block;
	}
	.image-choices-choice-text {
		display: none;
	}

	.image-choices-choice-image-wrap {
		display: inline-block !important;
		background-repeat: no-repeat;
		background-position: 50%;
		background-size: cover;
		line-height: 0;
		width: 100px;
		height: 100px;
		text-align: center;
		transition: all 0.3s;
	}
	.image-choices-choice-image {
		display: none;
	}


	&.image-choices-layout-vertical {
		// GF < 2.5
		ul.gfield_radio,
		ul.gfield_checkbox {
			display: inline-block;
			> li.image-choices-choice {
				display: block;
			}
		}
		// GF 2.5+
		div.gfield_radio,
		div.gfield_checkbox {
			display: inline-block;
			> .image-choices-choice {
				display: block;
			}
		}
	}

	&.image-choices-show-labels {
		.image-choices-choice-text {
			display: block;
		}
	}


	.image-choices-choice-selected {
		border-color: #000;
	}
	.image-choices-choice-focus label {
		border-color: #000;
	}

}
