@import "legacy/mixins";
@import "admin-common";

.image-choices-admin-field.image-choices-use-images {
	@include legacy_image_choices_field();
}

#gfield_settings_choices_container.choice_with_price.image-choices-enabled .field-choice-input {
	flex-basis: calc(50% - 6rem);
	width: calc(50% - 6rem);
}
#gfield_settings_choices_container.choice_with_value_and_price.image-choices-enabled .field-choice-input {
	flex-basis: calc(33.332% - 4rem);
	width: calc(33.332% - 4rem);
}

.button.image-choices-option-upload-button,
.image-choices-option-preview-wrap,
.image-choices-setting-title,
.image-choices-setting-use-images,
.image-choices-setting-show-labels,
.image-choices-setting-use-lightbox,
.image-choices-setting-show-prices,
.image-choices-setting-layout,
.image-choices-setting-entry-value {
	display: none !important;
}


/* Both GF 2.5 + and GF < 2.5 */
#field_settings.image-choices-field-settings,
#field_settings_container.image-choices-field-settings,
#gfield_settings_choices_container.image-choices-field-settings,
#gquiz_gfield_settings_choices_container.image-choices-field-settings,
.toplevel_page_gf_edit_forms .image-choices-admin-field {

	.image-choices-setting-use-images {
		display: block !important;
	}

	.image-choices-enabled,
	&.image-choices-enabled {
		.button.image-choices-option-upload-button {
			display: inline-block !important;
		}
		.image-choices-has-image {
			.image-choices-option-preview-wrap {
				display: inline-block !important;
			}
			.image-choices-option-upload-button {
				display: none !important;
			}
		}
	}

	&.image-choices-use-images {

		&[class*="gfield_product"] .image-choices-setting-show-prices,
		.image-choices-setting-title,
		.image-choices-setting-show-labels,
		.image-choices-setting-use-lightbox,
		.image-choices-setting-layout,
		.image-choices-setting-entry-value {
			display: block !important;
		}

	}

}

.toplevel_page_gf_edit_forms .image-choices-admin-field {

	.image-choices-enabled {

		.gfield_choice_header_label {
			padding-left: 154px;
		}
		.field-choice-input.field-choice-text {
			margin-left: 6px;
			width: 210px !important;
		}

		&.choice_with_value {
			.field-choice-input.field-choice-text,
			.field-choice-input.field-choice-value {
				width: 115px !important;
			}
			.gfield_choice_header_value {
				padding-left: 75px;
			}
		}
	}

	&.gsurvey-survey-field,
	&.gfield_price {
		// TODO: use the .image-choices-use-images class instead
		.image-choices-enabled {
			&,
			&.choice_with_price,
			&.choice_with_value {
				.field-choice-input.field-choice-text {
					width: 209px !important;
				}
			}
			&.choice_with_price {
				.field-choice-input.field-choice-text {
					width: 150px !important;
				}
				.field-choice-input.field-choice-price {
					width: 80px!important;
				}
			}
			&.choice_with_value_and_price {
				.field-choice-input.field-choice-text,
				.field-choice-input.field-choice-value {
					width: 80px !important;
				}
				.field-choice-input.field-choice-price {
					width: 70px!important;
				}
			}
		}
	}

	&.image-choices-use-images {
		.ginput_container {
			pointer-events: none;
		}
		ul.gfield_radio,
		ul.gfield_checkbox {
			> li {
				padding: 4px;
				margin: 0 4px 4px 0;
			}
		}
		.image-choices-choice-image-wrap {
			width: 50px;
			height: 50px;
		}

	}

	&:not(.image-choices-use-images) {
		.image-choices-choice-image-wrap {
			display: none;
		}
	}

	&.gquiz-field .gquiz-setting-choices.field_setting {
		.image-choices-enabled.gquiz-weighted-score {
			.field-choice-text {
				width: 182px !important;
			}
		}
	}


	&.gpoll_field .image-choices-enabled.choice_with_value {
		.field-choice-input.field-choice-text {
			width: 207px !important;
		}
	}

}

// Entries views
.gf_entries .entry_row/* entries table */,
.entry-detail-view/* entry detail GF < 2.5 */,
.entry-details-table/* entry detail GF 2.5+ */ {
	.image-choices-selected-image {
		display: inline-block;
		max-width: 100%;
		width: 100px;
		height: 100px;
	}

	.gf-image-choices-entry {
		list-style: none;
		//display: flex;
		display: block;
		text-align: center;
	}
	.gf-image-choices-entry > li,
	.gf-image-choices-entry-choice {
		//flex: 1;
		display: inline-block;
		vertical-align: top;
		margin: 0 10px 20px;
		text-align: center;
	}

	.gf-image-choices-entry-choice-image {
		display: inline-block;
		width: 80px;
		height: 80px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50%;
	}
	.gf-image-choices-entry-choice-text {
		display: block;
		font-size: 12px;
	}

	.gquiz-field-choice .gf-image-choices-entry-choice-text + img,
	.gquiz-field-choice .gf-image-choices-entry-choice-image + img {
		display: block;
		margin: 0 auto;
	}
	.gquiz-field-choice .gf-image-choices-entry-choice-image + img {
		margin-top: 5px;
	}
	.gpoll_choice_label {
		padding-left: 30px;
		margin-top: 25px;
		.gf-image-choices-entry-choice {
			display: block;
			text-align: left;
			margin: 0 10px 10px 0;
		}
		.gf-image-choices-entry-choice-image {
			margin-right: 10px;
		}
		.gf-image-choices-entry-choice-image,
		.gf-image-choices-entry-choice-text {
			display: inline-block;
			vertical-align: middle;
		}
	}
	.gpoll_field_label_container + .gpoll_choice_label {
		margin-top: 10px;
	}
}

#gf_image_choices_custom_css_global,
#gf_image_choices_custom_css {
	display: none;
}
#gf_image_choices_custom_css_global_editor,
#gf_image_choices_custom_css_editor {
	position: relative;
	width: 100%;
	height: 400px;
}
